.plmain__icons-icon-a {
  width: 45px;
  height: 45px;
  font-size: 20px;
  background-color: #505050;
  box-shadow: 0 5px 15px -5px #000000;
  border-radius: 30%;
  color: whitesmoke;
  position: relative;
  margin: 0.5rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
.plmain__icons-icon {
  background-color: transparent;
  transition: 0.2s;
  width: 45px;
  position: absolute;
}
.plmain__icons-icon-a:hover .plmain__icons-icon {
  transform: scale(1.3);
  color: #505050;
}
.plmain__icons-icon-a::before {
  content: "";
  position: absolute;
  width: 120%;
  height: 120%;
  background-color: whitesmoke;
  left: -110%;
  top: 90%;
  transform: rotate(45deg);
}
.plmain__icons-icon-a:hover::before {
  animation: animation 0.7s 1 forwards;
}
@keyframes animation {
  0% {
    left: -110%;
    top: 90%;
  }
  50% {
    left: 15%;
    top: -30%;
  }
  100% {
    top: -10%;
    left: -10%;
  }
}
@keyframes slideInFromRight {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0);
