  height: 4.5vw;
  overflow: hidden;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.plmain__core-title-rotate span {
  animation: animate 10s ease infinite;
  position: relative;
  color: #e58e26;
}
@keyframes animate {
  0% {
    top: 0;
  }
  20% {
    top: 0;
  }
  25% {
    top: -5vw;
  }
  45% {
    top: -5vw;
  }
  50% {
    top: -10vw;
  }
  70% {
    top: -10vw;
  }
  75% {
    top: -15vw;
  }
  95% {
    top: -15vw;
  }
  100% {
    top: 0;
  }
}
.plmain__core-paragraph {
  font-size: 1.8vw;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;

