  transition: 1s;
}
.plmain__core-paragraph:hover span {
  transform: translateY(-100%);
}
.plmain__core-paragraph:before {
  position: absolute;
  content: attr(data-link-alt);
  top: 100%;
  opacity: 0;
  transition: 1s;
}
.plmain__core-paragraph:hover:before {
  top: 0;
  opacity: 1;
}
