.plpcontainer {
  display: flex;
  flex-direction: row;
  height: auto;
}
.plpmain {
  display: flex;
  flex-direction: column;
}
@keyframes slideInFromTop {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateX(0);
  }
}
.plpmain__nav {
  display: flex;
  justify-content: space-between;
  animation: 1s ease-out 0s 1 slideInFromTop;
  position: fixed;
  overflow: hidden;
  z-index: 1;
}
.plpmain__nav-button {
  font-size: 20px;
  width: 100px;
  height: 40px;
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  margin-left: 2.9vw;
  border: 2px solid lightblue;
  color: whitesmoke;
  box-shadow: inset 0 0 0 0 lightblue;
  transition: ease-out 0.1s;
  cursor: pointer;
  outline: none;
}
.plpmain__nav-bar {
  display: flex;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-size: 20px;
  padding-right: 30px;
  padding-top: 30px;
  color: whitesmoke;
}
.plpmain__nav-bar p {
  padding-right: 1.5vh;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
.plpmain__nav-bar-pl {
  color: lightblue;
  padding-right: 1.5vh;
}
.plpmain__nav-bar-en,
.plpmain__nav-bar-es {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  transition: 0.2s;
  cursor: pointer;
}
.plpmain__nav-bar-en:hover,
.plpmain__nav-bar-es:hover {
  opacity: 0.5;
}
.plpmain__nav-button:hover {
  box-shadow: inset 0 100px 0 0 lightblue;
  color: #222831;
}
@keyframes slideInFromLeft {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}
.plpmain__icons {
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 5vh;
  padding-top: 35vh;
  padding-left: 0.8vw;
  padding-bottom: 1vh;
  animation: 1s ease-out 0s 1 slideInFromLeft;
  background-color: transparent;
  z-index: 2;
}
.plpmain__icons-icon-a {
  width: 45px;
  height: 45px;
  font-size: 20px;
  background-color: #505050;
  box-shadow: 0 5px 15px -5px #000000;
  border-radius: 30%;
  color: whitesmoke;
  position: relative;
  margin: 0.5rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
.plpmain__icons-icon {
  background-color: transparent;
  transition: 0.2s;
  width: 45px;
}
.plpmain__icons-icon-a:hover .plpmain__icons-icon {
  transform: scale(1.3);
  color: #505050;
}
.plpmain__icons-icon-a::before {
  content: "";
  position: absolute;
  width: 120%;
  height: 120%;
  background-color: whitesmoke;
  left: -110%;
  top: 90%;
  transform: rotate(45deg);
}
.plpmain__icons-icon-a:hover::before {
  animation: animation 0.7s 1 forwards;
}
@keyframes animation {
  0% {
    left: -110%;
    top: 90%;
  }
  50% {
    left: 15%;
    top: -30%;
  }
  100% {
    top: -10%;
    left: -10%;
  }
}
@keyframes slideInFromRight {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0);
  }
}
.plpmain__projects {
  padding-top: 9vh;
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  animation: 1s ease-in 0s 1 slideInFromRight;
}
